import OpenInNewIcon from "@mui/icons-material/OpenInNew";
import ThumbUpIcon from "@mui/icons-material/ThumbUp";
import { Box, Button, Link, Typography } from "@mui/material";
import countapi from "countapi-js";
import { useState } from "react";
import { Count } from "./Count";
import { Room } from "./Room";

export const LikePage = ({ room }: { room: Room }) => {
  const [timestamp, setTimestamp] = useState(0);

  const hit = async () => {
    countapi.hit(room.namespace, room.key);
    setTimestamp(Date.now());
  };

  return (
    <Box
      sx={{
        alignItems: "center",
        height: "100%",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        gap: "2rem",
      }}
    >
      <Typography variant="h4" sx={{ textTransform: "uppercase" }}>
        Likes Counter
      </Typography>
      <Box>
        <Button
          sx={{ fontSize: "1.2rem", width: "8rem" }}
          color="primary"
          size="large"
          onClick={() => hit()}
          variant="contained"
          startIcon={<ThumbUpIcon />}
        >
          Like
        </Button>
      </Box>
      <Count room={room} timestamp={timestamp} />
      <Box>
        <Link
          sx={{ display: "flex", alignItems: "center" }}
          href={`?ns=${room.namespace}&k=${room.key}&m=v`}
          target="_blank"
        >
          <OpenInNewIcon sx={{ mr: "0.2em" }} />
          <Typography>View Mode</Typography>
        </Link>
      </Box>
    </Box>
  );
};
