import { Fragment } from "react";
import { Helmet } from "react-helmet-async";
import { DSEG } from "./DSEG";
import { useDelayedCount } from "./hooks/useDelayedCount";
import { Room } from "./Room";

export const Count = ({
  room,
  minLength = 5,
  timestamp = 0,
}: {
  room: Room;
  minLength?: number;
  timestamp?: number;
}) => {
  const [count] = useDelayedCount(room, timestamp);

  return (
    <Fragment>
      <Helmet>
        <title>{`${count ?? 0} Likes Counter`}</title>
      </Helmet>
      <div
        css={{
          fontSize: "48px",
          display: "flex",
          flexDirection: "column",
          gap: "0.2em",
        }}
      >
        <DSEG color="#64ffda" value={"LIKES"} />
        <DSEG
          color="#64ffda"
          value={(count ?? 0).toString().padStart(minLength, "!")}
        />
      </div>
    </Fragment>
  );
};
